import React, { Fragment } from 'react'
import { withRouter, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import NProgress from 'nprogress'
import { Helmet } from 'react-helmet'
import Loader from 'components/LayoutComponents/Loader'
import PublicLayout from './Public'
import LoginLayout from './Login'
import MainLayout from './Main'

const Layouts = {
  public: PublicLayout,
  login: LoginLayout,
  main: MainLayout,
}

const mapStateToProps = state => {
  return {
    loading: state.user.loading,
    profile: state.firebase.profile,
    auth: state.firebase.auth,
    projectLoading: state.project.loading,
  }
}

@withRouter
@connect(mapStateToProps)
class IndexLayout extends React.PureComponent {
  previousPath = ''

  componentDidUpdate(prevProps) {
    const { location } = this.props
    const { prevLocation } = prevProps
    if (location !== prevLocation) {
      window.scrollTo(0, 0)
    }
  }

  render() {
    const {
      children,
      location: { pathname, search },
      loading,
      profile,
      auth,
    } = this.props

    // NProgress Management
    const currentPath = pathname + search
    if (currentPath !== this.previousPath) {
      NProgress.start()
    }

    setTimeout(() => {
      NProgress.done()
      this.previousPath = currentPath
    }, 300)

    // Layout Rendering
    const getLayout = () => {
      if (pathname === '/') {
        return 'public'
      }
      if (/^\/user(?=\/|$)/i.test(pathname)) {
        return 'login'
      }
      return 'main'
    }

    const Container = Layouts[getLayout()]
    const isUserAuthorized = auth.uid
    const isUserLoading = loading
    const isLoginLayout = getLayout() === 'login'

    const BootstrappedLayout = () => {
      if (!profile.isLoaded) {
        return <Loader />
      }
      // redirect to login page if current is not login page and user not authorized
      if (!isLoginLayout && isUserAuthorized === undefined) {
        return <Redirect to="/user/login" />
      }
      // show loader when user in check authorization process, not authorized yet and not on login pages
      if (isUserLoading && isUserAuthorized === undefined && !isLoginLayout) {
        return <Loader />
      }

      // redirect to main dashboard when user on login page and authorized
      if (isLoginLayout && isUserAuthorized) {
        return <Redirect to="/dashboard" />
      }
      // in other case render previously set layout
      return <Container>{children}</Container>
    }
    return (
      <Fragment>
        <Helmet titleTemplate="Clean UI React Pro | %s" title="React Admin Template" />
        {BootstrappedLayout()}
      </Fragment>
    )
  }
}

export default IndexLayout
